body {
  margin-top: 200px;
}

#lumenscroll {
  width: 300px;
  max-width: calc(100% - 200px);
  height: 300px;
  border: 1px solid #dedede;
  margin: 0 auto;
  padding: 10px;

  @media (max-width: 400px) {
    padding: 30px;
  }
}

#small-lumenscroll {
  width: 200px;
  height: 200px;
  border: 1px solid #dedede;
  margin: 20px auto;
  padding: 10px;
}
